<template>
    <!--<baidu-map class="bm-container" ak="O8GdXEXAGbDo3GA3dwkIcXvcGQFlRaS9" :center="bdMap.center" :zoom="bdMap.zoom" @ready="handlerBdMap" :scroll-wheel-zoom="bdMap.scroll_wheel_zoom">-->
      <!--&lt;!&ndash;</bm-marker>&ndash;&gt;-->
    <!--</baidu-map>-->
  <baidu-map :center="center" :zoom="zoom" class="bm-view" ak="O">
  </baidu-map>
</template>

<script>
import BaiduMap from 'vue-baidu-map/components/map/Map.vue'
export default {
  name: 'mapmenu',
  components: {
    BaiduMap
  },
  data () {
    return {
      center: '北京',
      zoom: 3
    }
  },
  mounted () {
    this.zoom = 10
  }
}
</script>

<style scoped>
  .bm-view {
    width: 100%;
    height: 700px;
  }
</style>



